.containerHeader {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background-color: #75dbcd;
}

.containerHeader h1 {
    font-family: 'Futura PT', sans-serif;   
    font-size: 2.1rem;                                             
    font-weight: 800;
    margin-top: 1rem;
}

.containerHeader h2 {
    font-family: 'Futura PT', sans-serif;                                                
    font-size: 1.999rem;
    font-weight: 300;  
}

.buttonCTA {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.buttonCTA button {
    font-size: 1.5rem;
    padding: .5rem 2rem;
}
